.TodoList {
    margin: 4rem auto;
    padding: 2rem 3rem 3rem;
    max-width: 500px;
    background: #ff6666;
    box-shadow:-20px -20px 0px 0px rgba(100,100,100,.1);
}
.TodoList ul {
    margin-top:2.6rem;
    list-style:none;
}

.TodoList h1 {
    /*text-align:center;*/
    font-weight:normal;
    font-size:2.6rem;
    letter-spacing:0.05em;
    border-bottom:1px solid rgba(255,255,255,.3); 
}

.TodoList h1 span {
  display:block;
  font-size:0.8rem;
  margin-bottom:0.7rem;
  margin-left:3px;
  margin-top:0.5rem;   
}